.button {
    margin-top: 10px;
    padding: 6px 22px;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: white;
    border-radius: 10px;
    background-color: grey;
    cursor: pointer;
  }
  
  .button:not(:last-child) {
    margin-right: 10px;
  }
  
  .button:hover {
    background-color: lightblue;
  }